<template>
	<div class="cart">
		<div class="nav">
			<div class="item"></div>
			<div class="item">购物车</div>
			<div class="item">编辑</div>
		</div>
		<div class="wrap">
			<ul>
				<li class="wrap-item" v-for='(item,i) in carts'>
					<div class="item-l">
						<img :src="item.img" alt="">
					</div>
					<div class="item-r">
						<p class="seller">{{item.shopSeller}}</p>
						<p class="title">{{item.title}}</p>
						<p class="price">{{item.price}}元</p>
					</div>
					
					
				</li>
			</ul>
		</div>
		<div class="emptyCarts" @click='emptyCarts' v-show="carts.length != 0">
			全部删除
		</div>
		<!-- 购物车为空 -->
		<div class="car" v-show="carts.length == 0">
			<div class="car-img">
				this is car
			</div>
			<div class="empty">购物车还是空的哦~~</div>
			<div class="go">去逛逛</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';

	export default{
		computed:{
			...mapGetters([
				'carts'
			])
		},
		methods:{
			emptyCarts(){
				this.$store.dispatch('emptyCarts');
			}
		}
	}

</script>

<style lang="less" scoped>
	.cart{
		position: fixed;	
		top: 0;
		left: 0;
		right: 0;
		bottom: 1rem;
		overflow: hidden;
		.nav{
			width: 100%;
			height: 1rem;
			border-bottom: 1px solid #e5e5e5;
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			line-height: 1rem;
			text-align: center;
			.item{
				flex:1;
			}
			.item:last-child{
				text-align: right;
				margin-right: 0.2rem;
				box-sizing: border-box;
			}			
			.item:nth-child(2){
				font-size: 0.4rem;
			}
		}
		.emptyCarts{
			text-align: center;
		}
		.wrap{
			padding: 0.2rem;
			.wrap-item{
				height: 2rem;
				width: 94%;
				display: flex;
				background: #e1e1e1;
				padding: 0.2rem;
				margin-top: 0.2rem;
				.item-l{
					flex:1;
					img{
						width: 2rem;
						height: 2rem;
					}
				}
				.item-r{
					flex:3;
					line-height: 0.5rem;
					padding-left: 0.3rem;
					.seller{
						font-size: 0.3rem;
					}
					.title{
						height: 0.4rem;
						line-height: 0.3rem;
						margin-top: 0.2rem;
					}
					.price{
						margin-top: 0.2rem;
						color: red;
					}


				}
			}
		}
		.car{
			width: 50%;
			height: 4rem;
			margin: 3.2rem auto;
			.car-img{
				width: 2rem;
				height: 2rem;
				line-height: 2rem;
				color: #ff5777;
				margin:0 auto;
				border-radius: 50%;
				text-align: center;
				background: #ddd5d7;
			}
			.empty{
				text-align: center;
				margin:0.3rem 0;
			}
			.go{
				width: 2rem;
				height: 0.7rem;
				border-radius: 5px;
				margin:0 auto;
				text-align: center;
				line-height: 0.7rem;
				color: #fff;
				background: #ff5777;
			}
		}
	}
</style>


