// 购物车
import TYPES from '../types.js'

const state = {
	carts : []
}

const getters = {
	carts( state ){
		return state.carts;
	}
}

const actions = { 
	// 加入购物车
	addCart( { commit , state } , item ){
		commit( TYPES.GET_CART , item );
	},
	emptyCarts( { commit } ){
		commit('emptyCarts');
	}
}

const mutations = {
	[TYPES.GET_CART]( state , item ){
		state.carts.push(item);
	},
	emptyCarts(state){
		state.carts = [];
		console.log("cciscc")
	}
}

export default {
	state ,
	getters ,
	actions ,
	mutations ,
}


