<template>
	<div class="detail" v-show='searchShow'>
		<div class="back">
			<div class="back-l" @click='hideSearch'>
				&lt;
			</div>
			<div class="back-c">
				<input type="text" v-model='ccCode'>
			</div>
			<div class="back-r" @click='getCode'>
				搜索
			</div>
		</div>
		<div class="history">
			<div class="tips">
				<div class="tip">
					历史记录
				</div>
				<div class="del">
					del
				</div>
			</div>
			<div class="tip-wrap">
				<span v-for='(i,index) in cc'>{{i}}</span>
			</div>
		</div>
		<div class="hot">
			<div class="hotSearch">
				热门搜索
			</div>
			<div class="hotHistory" v-if='searchResult.hotWord'>
				<!-- {{searchRe}} -->
				<span v-for='(i,index) in searchRe' :style="{'color':i.color}">
					{{i.query}}
				</span>
				
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'

	export default{
		data(){
			return {
				cc : ['cc'],
				ccCode : ''
			}
		},
		computed:{
			...mapGetters([
				'searchShow',
				'searchResult'
			]),
			searchRe(){
				return this.searchResult.hotWord.data;
			}
		},
		methods:{
			hideSearch(){
				this.$store.dispatch('hideSearch');
			},
			getCode(){
				this.cc.push(this.ccCode)
			}
		}
	}
</script>

<style lang="less" scoped>
	.detail{
		position: fixed;
		z-index:9999;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background: #fff;
		.back{
			height: 0.68rem;
			border-bottom: 1px solid #eee;
			display: flex;
			justify-content: space-between;
			line-height: 0.68rem;
			.back-l{
				width: 0.8rem;
				text-align: center;
				font-size: 0.5rem;
				color: #eee;
			}			
			.back-c{
				width: 5rem;
				text-align: center;
				font-size: 0.5rem;
				color: #eee;
				input{
					width: 100%;
					outline: none;
					height: 0.36rem;
					border: 2px solid #eee;
					border-radius: 5px;
					font-size: 0.22rem;
				}
			}
			.back-r{
				width: 0.6rem;
				text-align: center;
				font-size: 0.24rem;
				color: #999;
				margin-right: 0.2rem;
			}
		}
		.history{
			width: 100%;
			height: auto;
			margin-top: 0.2rem;
			padding: 0.2rem;
			.tips{
				display: flex;
				width: 92%;
				justify-content: space-between;
				height: 0.6rem;
			}
			.tip-wrap{
				width: 92%;
				padding: 0.2rem;
				span{
					border: 1px solid #eee;
					padding: 0.1rem;
					margin-top: 0.1rem;
					display: inline-block;
					border-radius: 4px;
				}
			}
		}
		.hot{
			width: 100%;
			.hotSearch{
				height: 0.4rem;
				font-size: 0.26rem;
				margin-left: 0.2rem;
			}
			.hotHistory{
				width: 92%;
				padding: 0.2rem;
				span{
					border: 1px solid #eee;
					padding: 0.1rem;
					margin-top: 0.2rem;
					display: inline-block;
					border-radius: 4px;
				}
			}
		}
	}
</style>


