<template>
	<div class="goods" ref='goodsShow' v-show='goodsShow'>
		<div class="wrap">
			<!-- 顶部图片 -->
			<div class="head">
				<div class="back" @click.stop='hideGoods'>&lt;</div>
				<img :src="goodDetail.img" alt="">
			</div>
			<!-- 标题 -->
			<div class="title">
				{{goodDetail.title}}
			</div>
			<!-- 价格 -->
			<div class="price">
				<p class="old">价格:<span>{{goodDetail.orgPrice}}</span></p>
				<ul>
					<li>
						<p>快抢价:<span>￥{{goodDetail.price}}</span></p>
					</li>
					<li>
						评价:<span>{{getRatings.total}}</span>
						累计销量:<span>{{goodDetail.sale}}</span>
					</li>
				</ul>
			</div>
			<div class="shop">
				<p>
					<span>商家名称</span>
					<span>{{goodDetail.shopSeller}}</span>
				</p>
			</div>
			<hr>
			<!-- 产品参数 -->
			<div class="props">
				<div class="tip">产品参数</div>
				<div class="wrap">
					<span v-for='item in goodDetail.props'>
						{{item}}
					</span>
					
				</div>
			</div>
		<!-- {{getRatings}} -->
			<div class="ratings">
				<p class="titles">所有评价</p>
				<div class="score">
					<div class="score-left">
						<p>综合评分</p>
						<p class="colors">4.94</p>
						<p>
							<span v-for='item in 5'></span>
						</p>
					</div>
					<div class="score-right">
						<p>描述相符:<span class="colors">4.97</span></p>
						<p>描述相符:<span class="colors">4.97</span></p>
					</div>
				</div>
				<div class="rating-wrap">
					<ul>
						<li class="ratings-item" v-for='(item,i) in getRatings.list'>
							<div class="rating-left">
								<img :src="item.user.avatar" alt="">
							</div>		
							<div class="rating-right">
								<p class="name">{{item.user.uname}}</p>
								<p class="content">{{item.content}}</p>
								<p class="time"><span class="times">{{item.time}}</span><span class="style">{{item.style}}</span></p>
								<p v-if='item.append' class="append">{{item.append.content}}</p>
<!-- 								<ul>
									<li v-if='item.images' v-for='(item,i) in item.images'>
										<img :src="item" alt="">
									</li>
								</ul> -->
							</div>		
						</li>
					</ul>
				</div>
			</div>
		</div>
		<div class="cart">
			<div class="one">
				收藏
			</div>
			<div class="two">
				小店
			</div>
			<div class="three" @click='addCart(goodDetail , $event)'>
				{{gocart}}
			</div>
			<div class="four">
				立即购买
			</div>
		</div>

	</div>
</template>

<script>
	
	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'

	export default{
		data(){
			return {
				gocart : '加入购物车'
			}
		},
		computed:{
			...mapGetters([
				'goodsShow',
				'goodDetail',
				'getRatings'
			])
		},
		watch:{
			goodDetail(){
				this.goodDetailBscroll = new BScroll( this.$refs.goodsShow , {
					click : true
				})
			}
		},
		methods:{
			// 关闭商品详情页
			hideGoods(){
				this.$store.dispatch('hideGoods');
			},
			//添加购物车
			addCart( item , e){

				clearTimeout(this.timer);
				this.timer = setTimeout( () => {
					if(!e._constructed) {
						return
					}
					// console.log(e)
					this.$store.dispatch('addCart',item);
				},0)
			}
		}
	}
</script>

<style lang="less" scoped>
	.goods{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: #fff;
		z-index: 999;
		.wrap{
			width: 7.5rem;
			.head{
				position: relative;
				width: 100%;
				padding-top: 100%;
				.back{
					position: absolute;
					width: 0.4rem;
					height: 0.4rem;
					top: 0.3rem;
					left: 0.3rem;
					z-index: 9;
					text-align: center;
					line-height: 0.4rem;
					font-size: 0.3rem;
					background: rgba(0,0,0,0.1);
					border-radius: 4px;
					color: #fff;
				}
				img{
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
				}
			}
			.title{
				height: 0.8rem;
				line-height: 0.5rem;
				padding: 0.2rem;
				font-size: 0.4rem;
				font-weight: 700;
			}
			.price{
				padding: 0.2rem;
				background: #f6f6f6;
				margin-top: 0.2rem;
				.old{
					height: 0.4rem;
					line-height: 0.4rem;
					font-size: 0.24rem;
					span{
						font-size: 0.3rem;
						text-decoration: line-through;
						margin-left: 0.2rem;
					}
				}
				ul{
					height: 0.6rem;
					line-height: 0.6rem;
					display: flex;
					width: 100%;
					li:first-child{
						flex:5;
						color: #ef2f23;
						p{
							span{
								font-size: 0.3rem;
								font-weight: 800;
							}
						}
					}
					li:last-child{
						flex:5;
						span:first-child{
							margin-right: 0.2rem;
						}
					}
				}
			}
			.shop{
				p{
					padding: 0.2rem;
					span:first-child{
						font-size: 0.36rem;
					}
					span:last-child{
						font-size: 0.3rem;
						color: #888;
						margin-left: 0.3rem;
					}
				}
			}
			.props{
				padding: 0.2rem;
				.tip{
					height: 0.4rem;
					line-height: 0.4rem;
					color:#999;
				}
				.wrap{
					span{
						display: inline-block;
					    height: .48rem;
					    line-height: .48rem;
					    border-radius: .04rem;
					    font-size: .24rem;
					    padding: 0 .12rem;
					    margin-right: .1rem;
					    margin-top: .1rem;
					    color: #f25;
   	 					background-color: #ffe8ee;
					}
				}
			}
			.ratings{
				padding: 0.16rem;
				.titles{
					font-size: 0.4rem;
					text-align: center;
					height: 0.5rem;
					line-height: 0.5rem;
					padding-bottom: 0.2rem;
					color: #777;
					border-bottom: 1px solid #c9c7c8;
				}
				.score{
					width: 100%;
					padding: 0.2rem 0;
					display: flex;
					text-align: center;
					height: 1.6rem;
					background: #f6f6f6;
					.colors{
						color: #fc5785;
					}
					.score-left{
						flex:2;
						border-right: 2px solid #e5e5e5;
						line-height: 0.5rem;
					}
					.score-right{
						flex:3;
						line-height: 0.6rem;
						text-align: left;
						margin-left: 0.5rem;
					}
				}
			}
			.rating-wrap{
				.ratings-item{
					padding: 0.2rem;
					border-bottom: 1px solid #e5e5e5;
					display: flex;
					.rating-left{
						flex:1;
						img{
							width: 1rem;
							height: 1rem;
						}
					}
					.rating-right{
						line-height: 0.46rem;
						flex:4;
						.name{
							font-size: 0.28rem;
							color: #424242;
						}
						.content{
						    color: #424242;
						}
						.time{
							.style{
								margin-left: 0.2rem;
							}
						}
					}
				}
			}

		}
		.cart{
			position: fixed;
			bottom: 0;
			left: 0;
			height: 1rem;
			right: 0;
			border-top: 1px solid #e5e5e5;
			z-index: 99;
			display: flex;
			line-height: 1rem;
			text-align: center;
			background: #fff;
			.one{
				flex:1;
			}
			.two{
				flex:1;
			}
			.three{
				flex:2;
			    font-size: .28rem;
			    color: #ff5777;
			    background-color: #ffe6e8;
			}
			.four{
				flex:2;
				font-size: .28rem;
			    color: #fff;
				background: #ff468f;
			}
		}
	}
</style>


