<template>
	<!-- 限时促销 -->
	<div class="promotion">
		<div class="cc">
			超实惠-促销直达
		</div>
		<div class="promotion-main" v-if='Promotion[0]'>
			<div class="promotion-l">
			<h3>{{Promotion[0].title}}</h3>
			<p>{{Promotion[0].viceTitle}}</p>
			<div class="img">
				<img :src="Promotion[0].image" alt="">
			</div>
		</div>		
		<div class="promotion-r">
			<div class="pro-t cc" v-if='Promotion[1]'>
				<div class="item-l ">
					<p>{{Promotion[1].title}}</p>					
					<p>{{Promotion[1].viceTitle}}</p>					
				</div>
				<div class="item-r">
					<img :src="Promotion[1].image" alt="">
				</div>
			</div>
			<div class="pro-t pro-b" v-if='Promotion[2]'>
				<div class="item-l">
					<p>{{Promotion[2].title}}</p>					
					<p>{{Promotion[1].viceTitle}}</p>					
				</div>
				<div class="item-r">
					<img :src="Promotion[2].image" alt="">
				</div>
			</div>
		</div>		
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'

	export default{
		//促销直达
		mounted(){
			this.$store.dispatch('getPromotion');
		},
		computed:{
			...mapGetters([
				'Promotion'
			])
		}

	}
</script>

<style lang="less" scoped>
	.promotion{
		
		.cc{
			width: 100%;
			height: 0.7rem;
			line-height: 0.7rem;
			padding-left: 0.2rem;
		}
		.promotion-main{
			border-top:1px solid #E5E5E5;
			border-bottom:1px solid #E5E5E5;
			width: 7.5rem;
			height: 3.2rem;
			display: flex;
			.promotion-l{
				flex:1;
				height: 100%;
				border-right:1px solid #E5E5E5;
				text-align: center;
				h3{
					margin-top: 0.36rem;
					font-size: 0.28rem;
					line-height: .4rem;
					font-weight: 900;
	    			color: #333;
				}
				p{
					margin-top: 0.16rem;
					color: #ff5f7e;
				}
				.img{
					margin: 0 auto;
					width: 50%;	
					height: 2rem;
					img{
						width: 100%;
						height: 100%;
					}
				}
			}
			.promotion-r{
				flex:1;
				height: 100%;
				.pro-t{
					width: 100%;
					height: 50%;
					display: flex;
					text-align: center;
					&.cc{
						border-bottom:1px solid #E5E5E5;
					}
					.item-l{
						flex:3;
						margin-top: 0.4rem;
						p{
							line-height: 0.36rem;
						}
						p:last-child{
							font-size: 0.24rem;
							color: #ff5f7e;
						}
						p:first-child{
							font-size: 0.28rem;
							font-weight: 900;
						}
					}
					.item-r{
						flex: 3;
						img{
							margin-top:0.2rem;
							width: 80%;
							height: 80%;
						}
					}
				}
			}
		}
	}	
	
</style>



