// 主页
import TYPES from '../types.js'
import Vue from 'vue'

const state = {
	bannerItems : [],	//轮播图
	brandSale : [], 	//品牌特卖
	lastMinute : {},	//限时抢购
	Promotion:[],		//促销直达
	Guess:[],			//猜你喜欢
	searchShow : false,  //显示搜索详情
	searchResult : {}   //搜索关键词结果
}

//前台通过mapGetters获取
const getters = {
	bannerItems( state ){
		return state.bannerItems;
	},
	brandSale( state ){
		return state.brandSale;
	},
	lastMinute( state ){
		return state.lastMinute;
	},
	Promotion( state ){
		return state.Promotion;
	},
	Guess( state ){
		return state.Guess;
	},
	searchShow( state ){
		return state.searchShow;
	},
	searchResult( state ){
		return state.searchResult;
	}
}

const actions = { 
	// 轮播图
	getBanner( { state , commit } ){
		Vue.http.jsonp('https://mce.mogucdn.com/jsonp/multiget/3',{
			params:{
				pids : 51822,
			},
		}).then( ({ data : {data : { 51822 :{list} } } }) => {
			// console.log(list[0].startTime)
			commit(TYPES.GET_BANNER,list);
		}).catch( err => {
			console.log(err)
		})
	},

	//品牌特卖
	getBrandSale( { state , commit } ){
		Vue.http.jsonp('https://mce.mogucdn.com/jsonp/multiget/3',{
			params:{
				pids : 51827,
			},
		}).then( ( {data : {data :{51827 : {list}} }}) => {
			// console.log(list)
			commit(TYPES.GET_BRANDSALE,list);
		}).catch( err => {
			console.log(err)
		})
	},

	// 限时抢购
	getLastMinute( { state , commit } ){
		Vue.http.jsonp('https://mce.mogucdn.com/jsonp/multiget/3',{
			params:{
				pids : 41119,
			},
		}).then( ({data : { data : { 41119 : {list}} }}) => {
			// console.log( list[0] )
			commit(TYPES.GET_LASTMINUTE,list[0]);
		}).catch( err => {
			console.log(err)
		})
	},

	//促销直达
	getPromotion( { state , commit } ){
		Vue.http.jsonp('https://mce.mogucdn.com/jsonp/multiget/3',{
			params:{
				pids : 51833    ,
			},
		}).then( ({data : { data : { 51833     : {list}} }}) => {
			commit(TYPES.GET_PROMOTION,list);
		}).catch( err => {
			console.log(err)
		})
	},

	// 猜你喜欢
	getGuess( { state , commit } ){
		Vue.http.jsonp('https://list.mogujie.com/search',{
			params:{
				pid : 9750,
				page : 1
			},
		}).then( ({ data : { result : { wall : { list } } } }) => {
			console.log(list[0].iid)
			commit( TYPES.GET_GUESS , list );
		}).catch( err => {
			console.log(err)
		})
	},
	//显示搜索详情
	showSearch( { commit } ){
		commit( TYPES.GET_SEARCH_SHOW );
	},
	hideSearch( { commit } ){
		commit( TYPES.GET_SEARCH_HIDE );
	},
	// 获取搜索关键词
	getSearchResult( { commit , state } ){
		Vue.http.jsonp('https://list.mogujie.com/module/mget',{
			params:{
				code : 'sketch,hotWord'
			},
		}).then( ({ data : {data } }) => {
			// console.log(data)
			commit( TYPES.GET_SEARCH_RESULT , data );
		}).catch( err => {
			console.log(err)
		})
	}

}

const mutations = {
	// 轮播图
	[TYPES.GET_BANNER]( state , data ){
		state.bannerItems = data;
	},

	//品牌特卖
	[TYPES.GET_BRANDSALE]( state , data ){
		state.brandSale = data;
	},

	//限时抢购
	[TYPES.GET_LASTMINUTE]( state , data ){
		state.lastMinute = data;
	},

	//促销直达
	[TYPES.GET_PROMOTION]( state , data ){
		state.Promotion = data;
	},

	// 猜你喜欢
	[TYPES.GET_GUESS]( state , data ){
		// console.log(data)
		state.Guess = data;
	},
	//显示搜索详情
	[TYPES.GET_SEARCH_SHOW]( state ){
		state.searchShow = true;
	},
	[TYPES.GET_SEARCH_HIDE]( state ){
		state.searchShow = false;
	},
	//搜索关键词结果
	[TYPES.GET_SEARCH_RESULT]( state , data ){
		state.searchResult = data;
	} 

}

export default {
	state ,
	getters ,
	actions ,
	mutations ,
}


