// 分类
import TYPES from '../types.js'
import Vue from 'vue'

const state = {
	Kinds : [],	//商品类别
	currentIndex : 0,
	kindItem : [], //商品类别大分类
	kindSelect : [], //商品分类类型
	kindItemDetail : [], //商品分类详情页
	selectBtn : [
					{sortKey: "pop", title: "综合", id : 0}, 
					{sortKey: "sell", title: "销量", id : 1}, 
					{sortKey: "new", title: "新品", id : 2}],
	cc : 0 ,
	tempKey : 0,
	goodsShow : true,	//展示商品详情页、
	goodDetail : {}	,	//商品详情信息
	getRatings : {}		//商品评价
}

const getters = {
	Kinds( state ){
		return state.Kinds;
	},
	currentIndex( state ){
		return state.currentIndex;
	},
	kindItem( state ){
		return state.kindItem;
	},
	kindSelect( state ){
		return state.kindSelect;
	},
	kindItemDetail( state ){
		return state.kindItemDetail;
	},	
	selectBtn( state ){
		return state.selectBtn;
	},
	cc( state ){
		return state.cc;
	},
	goodsShow( state ){
		return state.goodsShow;
	},
	goodDetail( state ){
		return state.goodDetail;
	},
	getRatings( state ){
		return state.getRatings;
	}

}

const actions = { 
	// 获取商品类别
	getKinds( { state , commit } ){
		Vue.http.jsonp('https://mce.mogucdn.com/jsonp/multiget/3',{
			params:{
				pids : 41789,
			},
		}).then( ({ data : {data : { 41789 :{list} } } }) => {
			// console.log(list)
			commit(TYPES.GET_KINDS,list);
		}).catch( err => {
			console.log(err)
		})
	},
	// 选择类别
	chooseKinds( { state , commit },{i,k}){
		Vue.http.jsonp('https://mce.mogujie.com/jsonp/makeup/3',{
			params:{
				pid : k,
			},
		}).then( ({ data : {data :{categoryNavigation : {list}} } }) => {
			console.log(k)
			commit( TYPES.GET_KINDS_ITEM , {i,k,list})
		}).catch( err => {
			console.log(err)
		})
	},
	// 选择类别详情页以及分类详情页
	chooseKindsDetail( { state , commit } , k ){
		// console.log(k)
		state.tempKey = k;
		Vue.http.jsonp('https://list.mogujie.com/search',{
			params:{
				cKey : 'h5-cube' ,
				fcid : k
			},
		}).then( ({ data : { result  } }) => {
			// console.log( result )
			commit( TYPES.GET_KINDSELECT , result )
		}).catch( err => {
			console.log(err)
		})
	},
	// 销量综合新品
	chooseOne( { commit , state } ,{key,i}){
		// console.log(key,i)
		// console.log(state.tempKey)
		// console.log(key)
		state.cc = i;
		Vue.http.jsonp('https://list.mogujie.com/search',{
			params:{
				cKey : 'h5-cube' ,
				fcid : state.tempKey,
				sort : key

			},
		}).then( ({ data : { result  } }) => {
			// console.log( result )
			commit( TYPES.GET_KINDSELECT , result )
		}).catch( err => {
			console.log(err)
		})
	},
	// 展示商品详情页
	showGoods( { commit } , item ){
		console.log(item)
		commit( TYPES.GET_SHOWFOOD , item );
	},
	hideGoods( { commit } ){
		commit( TYPES.GET_HIDEFOOD );
	},
	// 获取商品评论
	getRatings( { commit , state } , iid ){
		Vue.http.jsonp('https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1',{
			params:{
				itemId : iid
			},
		}).then( ({ data : { data } }) => {
			// console.log( data )
			commit( TYPES.GET_RATINGS , data )
		}).catch( err => {
			console.log(err)
		})
	}

}

const mutations = {
	// 获取商品类别
	[TYPES.GET_KINDS]( state , data ){
		state.Kinds = data;
	},
	// 选择类别
	[TYPES.GET_KINDS_ITEM]( state , {i,k,list} ){
		// console.log(i,k,list);
		state.currentIndex = i;
		state.kindItem = list;
	},
	[TYPES.GET_KINDSELECT]( state , result ){
		state.kindSelect = result.sortFilter;
		state.kindItemDetail = result.wall.docs;
		// console.log(state.kindItemDetail)
	},
	// 展示商品详情页
	[TYPES.GET_SHOWFOOD]( state , item ){
		state.goodsShow = true;
		state.goodDetail = item;
	},
	[TYPES.GET_HIDEFOOD]( state ){
		state.goodsShow = false;
	},
	// 获取商品评价
	[TYPES.GET_RATINGS]( state , data ){
		state.getRatings = data;
	}
}

export default {
	state ,
	getters ,
	actions ,
	mutations ,
}








