<template>
	<div class="search">
		<div class="msg">
			<img src="./msg.png">
		</div>
		<div class="searchs" v-if='searchResult.sketch'>
			<input type="text" :placeholder="placeholder" @click='showSearch'>
		</div>
		<div class="cart">
			<img src="./cart.png">
		</div>
		<searchDetail></searchDetail>
	</div>
</template>

<script>
	import searchDetail from './searchDetail/searchDetail.vue'
	import { mapGetters } from 'vuex'

	export default{
		components:{
			searchDetail
		},
		methods:{
			showSearch(){
				this.$store.dispatch('showSearch');
			}
		},
		computed:{
			...mapGetters([
				'searchResult'
			]),
			placeholder(){
				return this.searchResult.sketch.data.query;
			}
		},
		mounted(){
			this.$store.dispatch('getSearchResult');
		}
	}

</script>

<style lang="less" scoped>
	.search{
		height: 1rem;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid #d0d8e0;
		box-sizing: border-box;
		.msg{
			width: 1.2rem;
			img{
				width: 0.46rem;
				height: 0.4rem;
				margin: 0.3rem;
			}
		}
		.searchs{
			width: 5.6rem;
			height: 60%;
			margin-top: 0.18rem;
			margin-left: -0.2rem;
			input{
				width: 100%;
				height: 100%;
				background: #eeeeee;
				text-indent: 0.6rem;
				font-size: 0.26rem;
				border-radius: 5px;
				background-image: url('./search.png');
				background-repeat: no-repeat;
				background-size: 8% 45%;
				background-position:2% 50%;
			}
		}
		.cart{
			width: 1rem;
			img{
				width: 0.5rem;
				height: 0.42rem;
				margin: 0.3rem 0 0 0.2rem;
			}
		}
	}
</style>


