import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/home/home.vue'
import Kinds from '../components/kinds/kinds.vue'
import Cart from '../components/cart/cart.vue'
import Me from '../components/me/me.vue'


Vue.use(Router)

export default new Router({
	routes: [
		{
			path : '/home',
			component : Home
		},
		{
			path : '/kinds',
			component : Kinds
		},
		{
			path : '/cart',
			component : Cart
		},
		{
			path : '/me',
			component : Me
		},
		{ path: '*',    //这里是路由重定向，比如页面加载时候进入首页  
      		redirect: '/home'  
    	}
	],
	linkActiveClass:'active'// 比如给路由一个选中后的样式为红色  那么这里就能用到了---.router-link-active{样式}）
})



