<template>
	<div class="kinds">
		<!-- 搜索页面 -->
		<Search></Search>
		<div class="kinds-wrap">

			<div class="kind" ref='kind'>
				<ul>
					<li v-for='(i,index) in Kinds' :key="index" class="item" @click='chooseKinds(index,i.maitKey,i.miniWallkey)' :class='{"active" : index === currentIndex}'>
						<p>{{ i.title }}</p>
					</li>
				</ul>
			</div>
			<div class="kinds-detail" ref='detail'>
				<div class="wrap">
					<div class="wrap-t">
						<div class="items" v-for='(i,index) in kindItem' :key="index">
							<div class="img">
								<img :src="i.image" alt="">
							</div>
							<p>{{i.title}}</p>
						</div>						
					</div>
					<div class="wrap-b">
						<div class="tab">
							<div class="item-wrap">
								<span class="item"  v-for='(i,index) in selectBtn' :key="index" :class='{"active": index == cc}' @click='chooseOne(i.sortKey,i.id)'>
									{{i.title}}
								</span>
							</div>							
						</div>
						<div class="details">
							<div class="items" v-for='(i,index) in kindItemDetail' :key="index" @click='showGoods(i,i.iid)'>
								<div class="img">
									<img :src="i.img">
								</div>
								<p class="title">{{i.title}}</p>
								<div class="price">
									<span>￥{{i.price}}</span>
									<span>{{i.sale}}</span>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<goodsDetail></goodsDetail>
	</div>
</template>

<script>

	import Search from '../templates/search/search.vue'
	import goodsDetail from '../templates/goodsDetail/goodsDetail.vue'
	import { mapGetters } from 'vuex'
	import BSscroll from 'better-scroll'

	export default{
		components:{
			Search,
			goodsDetail
		},

		mounted(){
			this.$store.dispatch('getKinds');
			this.chooseKinds(0,'41888');
			this.$nextTick( () => {
				let kindBScroll =  new BSscroll( this.$refs.kind ,{
					click : true
				});
				let detailBScroll =  new BSscroll( this.$refs.detail ,{
					click : true
				})
			})
		},
		computed:{
			...mapGetters([
				'Kinds',
				'currentIndex',
				'kindItem',
				'kindSelect',
				'kindItemDetail',
				'selectBtn',
				'cc'

			])
		},
		methods:{
			chooseKinds(i,k,sk){
				// 选择类别
				this.$store.dispatch('chooseKinds',{i,k});
				// 选择类别详情页以及分类详情页
				this.$store.dispatch('chooseKindsDetail',sk)
			},
			// 销量综合新品
			chooseOne(key,i){
				// console.log(k,i);
				this.$store.dispatch('chooseOne',{i,key});
			},
			// 展示衣服详情页 获取商品评价
			showGoods( item , iid ){
				// console.log(item);
				this.$store.dispatch('showGoods',item );
				// 获取商家评价
				// 根据i.iid获取商家评价
				// console.log(iid)
				this.$store.dispatch('getRatings' , iid );
			}
		}
	}
</script>

<style lang="less" scoped>
	.kinds{
		width: 7.5rem;
		height: 100%;
		.kinds-wrap{
			.kind{
				top: 1rem;
				bottom: 1rem;
				width: 1.8rem;
				position: fixed;
				overflow: hidden;
				.item{
			    	position: relative;
				    height: 0.9rem;
				    line-height: 0.9rem;
				    text-align: center;
				    font-size: 0.28rem;
			        color: #666666;
			        background: #f6f6f6;
			        &.active{
		        	    background-color: #fff;
					    color: #FF5777;
					    p{
							border-left: solid #FF5577 4px;
							font-weight: bold;
					    }
			        }
			        p{
		        	    position: absolute;
					    width: 80%;
					    height: 0.3rem;
					    top: 50%;
					    left: 50%;
					    margin-top: -0.15rem;
					    margin-left: -0.9rem;
					    line-height: 0.3rem;
			        }
				}
			}
			.kinds-detail{
				width: auto;
				padding-left: 1.8rem;
				top: 1rem;
				bottom: 1rem;
				position: fixed;
				overflow: hidden;
				z-index: -99;
				.wrap{
					overflow: hidden;
					.wrap-t{
						padding: 0 0.13rem;
						overflow: hidden;
						border-bottom: 1px solid #ddd;
						.items{
						    width: 1.76rem;
						    height: 1.9rem;
						    margin: 0.12rem 0;
							float: left;
							.img{
								width: 1.3rem;
								height: 1.3rem;
								margin:0 auto;
								img{
									width: 100%;
									height: 100%;
								}
							}
							p{
								text-align: center;
								margin-top: 0.16rem;
							    font-size: 0.24rem;
    							color: #666666;
							}
						}
					}
					.wrap-b{
						.tab{
							.item-wrap{
								width: auto;
								display: flex;
								justify-content:space-between;
								margin: 0.1rem 0 0.1rem;
								.item{
									width: 1.9rem;
									text-align: center;
									line-height: 0.6rem; 
									height: 0.6rem;
									border-right: 1px solid #e6e6e6;
									color: #333;
									&.active{
										color: #ef4562 
									}
								}
								.item:last-child{
									border-right: none;
								}
							}
						}
						.details{
							padding: 0 0.1rem;
							display: flex;
							justify-content: space-between;
							flex-wrap: wrap;
							.items{
								margin: 0.1rem 0;
								.img{
									width: 2.67rem;
									height: 3.5rem;
									img{
										width: 100%;
										height: 100%;
									}
								}
								.title{
									width: 2.6rem;
									height: 0.3rem;
									line-height: 0.3rem;
								    overflow: hidden;
									text-overflow: ellipsis;
									white-space: nowrap;
									font-size: 0.2rem;
									margin: 0.1rem 0;
								}
								.price{
									display: flex;
									justify-content: space-between;
									padding-right: 0.1rem;
									span:first-child{
										color: #FF5777;
										font-weight: 700;
									}
								}
								
							}
						}
					}
				}
			}
		}
	}
</style>


