<template>
	<div class="home" ref='home'>
		<div class="">
			<!-- 搜索框 -->
			<Search></Search>

			<!-- banner -->
			<Banner></Banner>

			<!-- 品牌特卖 -->
			<brandSale></brandSale>

			<!-- 分割线 -->
			<v-line></v-line>

			<!-- 限时抢购 -->
			<lastMinute></lastMinute>

			<!-- 分割线 -->
			<v-line></v-line>
			
			<!-- 限时促销 -->
			<Promotion></Promotion>

			<!-- 猜你喜欢 -->
			<Guess></Guess>

		</div>
	</div>
</template>

<script>
	// better-scroll
	import BScroll from 'better-scroll'
	import Search from '../templates/search/search.vue'
	import Banner from '../templates/banner/banner.vue'
	import brandSale from '../templates/brandSale/brandSale.vue'
	import Line from '../templates/line/line.vue'
	import lastMinute from '../templates/lastMinute/lastMinute.vue'
	import Promotion from '../templates/promotion/promotion.vue' 
	import Guess from '../templates/guess/Guess.vue'
	
	export default{
		components:{
			Search,
			Banner,
			brandSale,
			'v-line':Line,
			lastMinute,
			Promotion,
			Guess
		},
		mounted(){
			let scroll = new BScroll( this.$refs.home , {
				click : true
			})
		}
	}

</script>

<style lang="less" scoped>
	.home{
		position: fixed;
		top: 0;
		bottom: 1rem;
		overflow: hidden;
		.cc{
			width: 7.5rem;
			background: #ddd;
		}
	}
</style>


