import Vue from 'vue'
import App from './App'
import router from './router'

import store from './store/index.js'

// 模块中 加载  vue-resource 模块  会返回一个HTTP 对象
import http from 'vue-resource' 

//  会在 所有的 Vue 组件对象中 都注入  $http 属性 
//  用于发送 AJAX请求
// console.log(http);
Vue.use(http);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  // 根实例中注册 store 选项，该 store 实例会注入到根组件下的所有子组件中
  store,
  template: '<App/>',
  components: { App }
})



