<template>
	<div class="guess">
		<div class="bg">
			猜你喜欢
		</div>
		<div class="guess-wrap">
			<div class="item" v-for="(i,index) in Guess" :key="index">
				<div class="img">
					<img :src="i.show.img" height="226" width="172" alt="">
				</div>
				<div class="tips">
					<span v-for="(i,index) in i.props" :key="index">
						{{i}}
					</span>
				</div>
				<div class="price-wrap">
					<span class="tuan"></span>
					<span class="price">{{i.price}}</span>
					<span class="count">{{i.cfav | sale}}</span>
				</div>
			</div>	
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'

	export default {
		computed:{
			...mapGetters([
				'Guess'
			])
		},
		mounted(){
			// 获取猜你喜欢
			this.$store.dispatch('getGuess').then( (date) => {
				// console.log(data)
			})
		},
		filters:{
			sale(data){
				let num = parseInt(data);
				// console.log(num,typeof num);
				return num > 9999 ? (num/10000).toFixed(1)+"w":num;
			}
		}
	}
</script>

<style lang="less" scoped>
	.guess{
		width: 7.5rem;
		height: auto;
		background: #f6f6f6;
		.bg{
			width: 80%;
			height: 1.2rem;
			line-height: 1.2rem;
			text-align: center;
			margin:0 auto;
			background-image: url(./guess.png);
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: 60% 10%;		
			color: #333;
			font-size: 0.3rem;
		}
		.guess-wrap{
			width: 96%;
			margin:0 auto;
			height: auto;
			display: flex;
			justify-content: space-between;
			flex-wrap:wrap;
			.item{
				width: 3.5rem;
				height: auto;
				margin-top: 0.14rem;
				background: #fff;
				.img{
					width: 100%;
					border-radius: 5px 5px 0 0;
					height: 4.4rem;
					overflow: hidden;
					img{
						width: 100%;
						height: 100%;
					}
				}
				.tips{
					margin-top: 0.1rem;
					width: 94%;
					height: 0.9rem;
					// height: auto;
					overflow: hidden;
					span{
					    float: left;
					    height: .36rem;
					    line-height: .36rem;
					    padding: 0 .06rem;
					    margin: 0.02rem 0 .06rem .06rem;
					    font-size: .22rem;
					    background: #eff3f6;
					    color: #5a6f7a;
					}
				}
				.price-wrap{
					width: 96%;
					height: 0.4rem;
					line-height: 0.4rem;
					.tuan{
						width: 0.5rem;						
						height: 0.3rem;
						background: #ddd;
						display: inline-block;
						vertical-align: middle;
						margin-top: -0.1rem;
						margin-left:0.1rem;
						background-image: url(./buy.png);
						background-repeat: no-repeat;
						background-size: 100% 100%;
					}
					.price{
						font-size: 0.3rem;
					    font-weight: 700;
					    color: #333;
					    overflow: hidden;
					}
					.count{
					    font-size: .24rem;
					    color: #999;
					    overflow: hidden;
					    float: right;
					    width: 0.8rem;
					    margin-right: .12rem;
					    background-image: url(./star.png);
					    background-size: auto .24rem;
					    background-repeat: no-repeat;
					    background-position: 100% 28%;
					}
				}
			}
		}
	}
</style>


