<template>
	<div class="banner">
		<slider animation="normal" class='slider-wrap'
        :speed="1000"
        :control-btn=false>
			<!-- <p style="line-height: 1rem; font-size: 1rem; text-align: center;" v-if="!bannerItems.length">Loading...</p> -->
			<slider-item class='slider' v-for="(i, index) in bannerItems" :key="index">
				<div class="item">
					<!-- {{bannerItems}} -->
					<img :src="i.image_800">
				</div>
			</slider-item>
		</slider>
	</div>
</template>

<script>
	import { Slider, SliderItem } from 'vue-easy-slider'
	import { mapGetters } from 'vuex'

	export default {
		components:{
			Slider,
		    SliderItem
		},
		computed:{
			...mapGetters([
				'bannerItems'
			])
		},
		mounted(){
			// 获取轮播图
			this.$store.dispatch('getBanner');
		}
	}
</script>

<style lang="less" scoped>
	.banner{
		width: 7.5rem;
		height: 3rem;
		.slider-wrap{
			width: 100%;
			height: 3rem !important;
			.slider{
				height: 3rem;
				.item{
					height: 3.4rem;
					img{
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}
</style>


