<template>
	<div class="lastMinute">
		<div class="time">
			<div class="time-l">
				<p>
					{{lastMinute.title}}
					<span class="logo"></span>
					{{lastMinute.viceTitle}}	
				</p>
			</div>
			<div class="time-r">
				<span ref='hour'>00</span>:
				<span ref='minute'>00</span>:
				<span ref='second'>00</span>
			</div>
			<div class="more">
				<img src="./more.png">
			</div>
		</div>
		<div class="wrap" ref='wrap'>
			<ul ref='wrapW'>
				<li v-for = '(i,index) in lastMinute.list'>
					<div class="img">
						<img :src="i.img" alt="">
					</div>	
					<p class="title">可爱当发生第三方斯蒂芬飞多少</p>				
					<p class="price">
						<span class="new">￥35.00</span>
						<span class="old">￥114</span>
					</p>
				</li>	
			</ul>
		</div>
	</div>
</template>

<script>

	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'

	export default {
		data(){
			return {
				timer : null
			}
		},
		computed:{
			...mapGetters([
				'lastMinute'
			]),

		},
		mounted(){
			this.$store.dispatch('getLastMinute').then( () => {
				// console.log(78)
			});
		},
		beforeDestroy(){
			clearInterval( this.timer );
		},
		watch:{
			lastMinute(){
				this.$nextTick( () => {
					this.timer = setInterval(this.countDown,1000);
				})
			},
			lastMinute(){
				let picWidth = 90;
			    let margin = 10;
			    let width = ( picWidth + margin ) * this.lastMinute.list.length;
			    this.$refs.wrapW.style.width = width + 'px';
			    this.menuWrap = new BScroll( this.$refs.wrap ,{
			    	scrollX : true,
			    	click : true
			    });

			}
		},
		methods:{
			countDown(){
				this.lastMinute.time--;
				if(!this.lastMinute.time)
				{
					this.$store.dispatch('getLastMinute');
				}
				var hour = Math.floor(this.lastMinute.time / (60*60));		//小时
				var minute = Math.floor(this.lastMinute.time % (60*60) / 60);		//分钟
				var second =   this.lastMinute.time % 60;	//秒

				this.$refs.hour.innerHTML = hour < 10 ? "0" + hour : hour;
				this.$refs.minute.innerHTML = minute < 10 ? "0" + minute : minute;
				this.$refs.second.innerHTML = second < 10 ? "0" + second : second;
			}
		}
	}

</script>

<style lang="less" scoped>
	.lastMinute{
		width: 7.5rem;
		height: 3.26rem;
		box-sizing: border-box;
		.time{
			width: 7rem;
			height: 0.48rem;
			margin:0.2rem auto;
			box-sizing: border-box;
			line-height: 0.48rem;
			display: flex;
			justify-content: space-between;
			.time-l{
				width: 2.5rem;
				p{
					font-size: 0.28rem;
					span{
						width: 0.08rem;
					    height: 0.08rem;
					    display: inline-block;
					    background-color: black;
					    border-radius: 0.1rem;
					    margin: 0 0.12rem 0.05rem 0.12rem;
					}
				}
			}
			.time-r{
				width: 3rem;
				span{
				    display: inline-block;
    				color: #fff;
    				padding: 0 0.06rem;
    				border-radius: 0.06rem;
    				background: #000;
    				margin:auto 0.05rem;
				}
			}
			.more{
				margin-right: 0;
				img{
					width: 1rem;
					height: 0.24rem;
					vertical-align: middle;
				}
			}
		}
		.wrap{
			width: 7.5rem;
			height: 2.6rem;
			box-sizing: border-box;
			white-space: nowrap;
			ul{
				li{
					width: 1.8rem;
					height: 2.6rem;
					margin-left: 0.2rem;
					box-sizing: border-box;
					display: inline-block;
					.img{
						width: 90%;
						height: 1.rem;
						nargin:0 auto;
						img{
							width: 100%;
							height: 100%;
						}
					}
					.title{
						color: #666;
						width: 100%;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
						font-size: 0.2rem;
						margin-top: 0.2rem
					}
					.price{
						display: flex;
						height: 0.4rem;
						line-height: 0.4rem;
						margin-top: 0.1rem;
						.new{
							color: #f25;
						}
						.old{
							margin-left: 0.12rem;
							font-size:0.2rem; 
							text-decoration: line-through;
							color: #888;
						}
					}
				}
			}
		}
	}
</style>


