<template>
	<div class="brand">
		<div class="brand-box">
			<ul>
				<li v-for='(i,index) in brandSale' :key="index" class="brand-item"> 
					<h3 class="title">{{i.title}}</h3>
					<h3 class="description">{{i.description}}</h3>
					<div class="img-show">
						<img :src="i.image">
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'

	export default{
		// 品牌特卖
		mounted(){
			this.$store.dispatch('getBrandSale');
		},
		computed:{
			...mapGetters([
				'brandSale'
			])
		}
	}

</script>

<style lang="less" scoped>
	.brand{
		width: 7.5rem;
		height: 2.34rem;
		.brand-box{
			width: 6.6rem;
			margin: 0.2rem auto;
			line-height: 0.4rem;
			ul{
				display: flex;
				justify-content: space-around;
				text-align: center;
				.brand-item{
					.title{
						color: #ff6801;
						font-size: 0.26rem;
					}
					.description{
						color: #666;
						font-size: 0.24rem;
					}
					flex:1;
					.img-show{
						margin-top: 0.2rem;
						img{
							width: 1.1rem;
							height: 1.1rem;
						}
					}
				}
			}
		}
	}
</style>


